



























































import Vue from 'vue'
import Page from '@/components/Page.vue'

export default Vue.extend({
  data() {
    return {
      custom: false,
      mobile: '12345678907',
      code: '',
      agree: false,
      agreeAnimate: false,
    }
  },
  components: { Page },
  methods: {
    info(): void {
      this.custom = true
    },
    close(): void {
      this.custom = false
    },
    save(): void {
      if (!this.agree) {
        this.agreeAnimate = true
      } else {
        this.$toast.success('已完成')
      }
    },
  },
})
